import styled from "styled-components";

export const PrimaryButton = (
  props: React.ButtonHTMLAttributes<HTMLButtonElement>
) => <PrimaryWrapper {...props} />;

export const SecondaryButton = (
  props: React.ButtonHTMLAttributes<HTMLButtonElement>
) => <SecondaryWrapper {...props} />;

export const Button = styled.button((p) => ({
  display: "flex",
  alignItems: "center",
  justifyContent: "center",

  height: 32,
  border: "none",
  borderRadius: 8,

  ...p.theme.fonts["Body-Medium/Regular"],

  cursor: "pointer",
  outline: "none",
}));

const PrimaryWrapper = styled(Button)((p) => ({
  color: "white",
  padding: "0 20px",
  backgroundColor: p.theme.colors.primary,
}));

const SecondaryWrapper = styled(Button)((p) => ({
  color: p.theme.colors.primary,
  padding: "0 20px",
  backgroundColor: p.theme.colors.neutral_1,
}));
